.multi_container {
  background-color: purple;
  height: 100vh;
}
.m_quiz_container {
  display: block;
  height: 100%;
  width: 100%;
}
.join-grp-container {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.join-grp-div {
  padding: 20px 10px 10px 10px;
  background-color: white;
  border-radius: 5px;
  text-align: center;
  font-size: 15px;
  display: grid;
  grid-template-columns: 1fr;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  row-gap: 10px;
}
.m_quiz_container .btn {
  background-color: purple !important;
  border-color: black !important;
}
.m_quiz_container .timer {
  background-color: rgba(128, 0, 128, 0.411) !important;
  border-color: purple !important;
}
.m_quiz_container section .option {
  background-color: rgba(102, 51, 153, 0.096);
  border-color: purple !important;
}

.m_quiz_container section .option:hover {
  border: 1px solid rgb(143, 63, 143) !important;
  padding: 10px 15px;
  font-size: 18px;
}
.m_quiz_container .time_line {
  background-color: purple !important;
}

.join-grp-div h2 {
  color: black;
}
.join-grp-div input {
  font-size: 20px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  margin: 0 5px;
  padding: 10px 5px;
  border-color: rgb(34, 34, 34);
  text-align: center;
}

.join-grp-div .btn {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  text-decoration: none;
  padding: 20px;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
  border: none;
  outline: none;
  color: #fff;
  background: purple;
  border: 1px solid black;
  line-height: 10px;
  transform: scale(0.95);
  transition: all 0.3s ease;
}

.whoseTurnDiv {
  height: 6%;
  min-width: 350px;
  max-width: 550px;
  width: 60%;
  align-items: center;
  margin: auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
}
.whoseTurnDiv p {
  font-size: 16px;
  align-self: center;
  padding-bottom: 2px;
  vertical-align: middle;
  font-family: avin;
  font-weight: 600;
  color: white;
}
.whoseTurnDiv .messageBox {
  color: yellow;
  padding-bottom: 2px;
  vertical-align: middle;
  align-self: center;
  font-size: 16px;
  opacity: 1;
  margin-left: -10px;
}

.designedBy {
  color: azure;
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  padding-bottom: 40px;
}

.btnJoin {
  width: fit-content;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  text-decoration: none;
  padding: 10px 30px;
  font-size: 20px;
  font-weight: 400;
  color: purple;
  cursor: pointer;
  border: none;
  outline: none;
  background-color: white;
  border: 2px solid gray;
  transform: scale(0.95);
  transition: all 0.3s ease;
}

.addRadiusBelow {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.loader-container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  background-color: purple;
  display: flex;
}
.loader {
  border-bottom: 5px solid rgb(88, 1, 88) !important;
  border: 5px solid #f3f3f3; /* Light grey */
  border-top: 5px solid rgb(88, 1, 88); /* Blue */
  border-radius: 50%;
  width: 50px;
  margin: auto;
  height: 50px;
  animation: spin 2s linear infinite;
}

.loader-container span {
  position: absolute;
  bottom: 0;
  width: 100%;
  font-family: arvin;
  color: rgba(253, 232, 253, 0.582);
  text-align: center;
  margin-bottom: 50px;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
