}

/* importing google fonts */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
html {
  height: 100%;
}
body {
  height: 100%;
  padding: 0;
  margin: 0;
}

::selection {
  height: 100%;
  color: #fff;
  background: #007bff;
}

section {
  padding: 0 30px;
  background: #fff;
}

section .que_text {
  font-size: 20px;
  font-weight: 600;
  text-justify: inter-cluster;
}

section .option_list {
  padding: 20px 0px;
  display: block;
}

section .option_list .option {
  background: aliceblue;
  border: 1px solid #84c5fe;
  border-radius: 5px;
  padding: 8px 15px;
  font-size: 17px;
  text-align: left;
  margin-bottom: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

section .option_list .option:last-child {
  margin-bottom: 0px;
}

section .option_list .option:hover {
  color: #004085;
  background: #cce5ff;
  border: 1px solid #b8daff;
}

section .option_list .option.correct {
  color: #155724;
  background: #d4edda;
  border: 1px solid #c3e6cb;
}

section .option_list .option.incorrect {
  color: #721c24;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
}

section .option_list .option.disabled {
  pointer-events: none;
}

section .option_list .option .icon {
  height: 26px;
  width: 26px;
  border: 2px solid transparent;
  border-radius: 50%;
  text-align: center;
  font-size: 13px;
  pointer-events: none;
  transition: all 0.3s ease;
}
.option_list .option .icon.tick {
  color: #23903c;
  border-color: #23903c;
  background: #d4edda;
}

.option_list .option .icon.cross {
  color: #a42834;
  background: #f8d7da;
  border-color: #a42834;
}

footer {
  height: 60px;
  padding: 5px 30px;
  display: flex;
  align-items: center;
  align-self: flex-end;
  justify-content: space-between;
  border-top: 1px solid lightgrey;
}

footer .total_que span {
  display: flex;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

footer .total_que span p {
  font-weight: 500;
  padding: 0 5px;
}

footer .total_que span p:first-child {
  padding-left: 0px;
}

footer .button {
  height: 40px;
  padding: 0 13px;
  font-size: 18px;
  font-weight: 400;
  cursor: pointer;
  border: none;
  outline: none;
  color: #fff;
  border-radius: 5px;
  background: #007bff;
  border: 1px solid #007bff;
  line-height: 10px;
  pointer-events: none;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  transition: all 0.3s ease;
}

.buttons .restart:hover {
  background: #0263ca;
}

.buttons .quit:hover {
  color: rgba(255, 49, 49, 0.801);
  border-color: rgba(255, 49, 49, 0.801) !important;
}
.game_type .start_btn {
  padding: 10px;
}
.game_type .start_link {
  display: block;
}

.notification {
  padding: 10px;
  width: 100%;
  text-align: center;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.541);
  font-size: 15px;
  opacity: 0;
  color: white;
  position: absolute;
  top: 0;
  left: 0;
}
.hidden_fade {
  opacity: 0;
}

.fade-out {
  animation: fadeOut ease 8s;
  -webkit-animation: fadeOut ease 8s;
  -moz-animation: fadeOut ease 8s;
  -o-animation: fadeOut ease 8s;
  -ms-animation: fadeOut ease 8s;
}
@keyframes fadeOut {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
  }
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.start_btn {
