  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.start_link {
  text-decoration: none;
  font-size: 25px;
  font-weight: 500;
  color: #007bff;
  padding: 15px 30px;
