.result_container {
  background-color: #007bff;
  height: 100vh;
}
.change_bgToPurple {
  background-color: purple;
}
.result_box {
  position: absolute;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  pointer-events: auto;
  background: #fff;
  border-radius: 5px;
  display: flex;
  padding: 25px 30px;
  width: 80%;
  text-align: center;
  margin: 0 10%;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  transition: all 0.3s ease;
}

.result_box .icon {
  font-size: 100px;
