}

@media (max-width: 400px) {
  .result_box {
    width: 80%;
    margin: 0 10%;
    padding: 20px 15px 15px;
  }
  .result_box .buttons {
    display: flex;
    margin: 10px 0;
  }
  .result_box .buttons .btn {
    padding: 14px 10px;
    font-size: 16px;
  }
}

