.rules_container {
  background-color: #007bff;
  background: #007bff;
  height: 100vh;
  width: 100%;
}
.m_rules_container {
  background-color: purple;
  background: purple;
  height: 100vh;
  width: 100%;
}
.info_box {
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background: #fff;
  border-radius: 5px;
  transform: translate(-50%, -50%) scale(0.9);
  width: fit-content;
}

.info_box .info-title {
  height: 60px;
  width: 100%;
  border-bottom: 1px solid lightgrey;
  display: flex;
  align-items: center;
  padding: 0 30px;
  border-radius: 5px 5px 0 0;
  font-size: 20px;
  font-weight: 600;
}

.info_box .info-list {
  display: grid;
  grid-template-columns: 1fr auto;
  column-gap: 8px;
  padding: 15px 30px;
}
.info_box .info-list > span {
  align-self: center;
  font-size: 18px;
}

.info_box .info-list .info {
  margin: 8px 0;
  font-size: 17px;
}

.info_box .info-list .info span {
  font-weight: 600;
  color: #007bff;
}
.info_box .purple .info p span {
  font-weight: 600;
  color: purple !important;
}

.setCenter {
  padding-top: 20px;
  text-align: center;
}
.info_box .buttons {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 30px;
  border-top: 1px solid lightgrey;
}

.info_box .buttons .btn {
  text-decoration: none;
  margin: 0 5px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  border: none;
  text-align: center;
  padding: 8px 15px;
  outline: none;
  border-radius: 5px;
  border: 1px solid #007bff;
  transition: all 0.3s ease;
}

.buttons .restart {
  color: #fff;
  background: #007bff;
}
.purple-restart {
  border: 1px solid purple !important;
  color: #fff !important;
  background: purple !important;
}
.purple-quit {
  color: purple !important;
  background: #fff !important;
}
.buttons .quit {
  color: purple !important;
  background: #fff !important;
}
